<template>
    <div class='about clearfix'>
        <div class='aboout-index max'>
            <span>首页</span>
            <span>公司介绍</span>
        </div>
        <div class='max '>
            <ul class='fl' v-for='tab in tabs'>
                <li>{{tab.title}}</li>
            </ul>
            <div class="products">
                <div class="main selected">
                    关于公司介绍
                </div>
                <div class="main">
                    企业文化
                </div>
                <div class="main">
                    组织合作
                </div>
                <div class="main">
                    证书
                </div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            tabs: [{
                title: '关于斗文',
                path: ''

            }, {
                title: '企业文化',
                path: ''

            }, {
                title: '组织架构',
                path: ''

            }, {
                title: '资质证书',
                path: ''

            }, {
                title: '合作伙伴',
                path: ''

            }]
        }
    }
}
</script>

<style scoped>
.max {
    width: 1040px;
    margin: auto;
}

.aboout-index {
    height: 40px;
    line-height: 40px;
    border-bottom: 2px solid #2a3648;
    margin-bottom: 15px;
}

.aboout-index span {
    font-size: 15px
}

.max ul {
    width: 200px;
    float: left;
}

.max ul li {
    float: left;
    width: 100%;
    height: 36px;
    list-style: none;
    line-height: 36px;
    margin-left: 20px;
}

.max ul li li:nth-child(2n) {
    background: #2a3648;
}
</style>




